.item {
    outline: none;
}

.item-inner {
    background-image: linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)), url('https://www.gramedia.com/blog/content/images/2019/02/dilan-1990.jpeg');
    height: 250px;
    box-shadow: 10px 10px 5px rgba(0, 0, 0, 0.2);
    max-width: auto;
    background-position: center;
    background-size: cover;
    border-radius: 18px;
}

.item2 .item-inner {
    background-image: linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)), url('https://www.gramedia.com/blog/content/images/2019/06/five_feet_apart.jpg');
}

.item3 .item-inner {
    background-image: linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)), url('https://www.gramedia.com/blog/content/images/2019/06/rich_people_problem.jpg');
}

.item4 .item-inner {
    background-image: linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)), url('https://www.gramedia.com/blog/content/images/2019/10/9786237211280_batch.jpg');
}

.item5 .item-inner {
    background-image: linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)), url('https://www.gramedia.com/blog/content/images/2019/10/img20190913_14323955_qUA9DUp_batch.jpg');
}

.item-inner .text-slide {
    width:100%;
    position: absolute;
    bottom: 0;
    padding: 20px 25px;
    background-color: rgba(0, 0, 0, 0.2);
    font-size: 12px;
    color: #fff;
    letter-spacing: 1px;
    border-radius: 18px;
}

.item-inner .text-slide .title-slide {
    font-size: 20px;
    font-weight: bold;
}

.item-inner .text-slide .author-slide {
    font-size: 15px;
    border-bottom: 1px solid white;
    padding-bottom: 5px;
}

.slick-slide {
    transform: scale(0.8);
    opacity: 0.8;
    transition: all ease 0.5s;
}

.slick-current {
    transform: scale(1);
    opacity: 1;
}
